const StakeInput = () => {
    const { connection } = useConnection();
    const { publicKey, signTransaction, sendTransaction } = useWallet();
    const wallet: any = useWallet();

    const [tokenBalance, setTokenBalance] = useState<any>(null);
    const [tokenBalanceOriginal, setTokenBalanceOriginal] = useState<any>(null);
    const [stakeAmount, setStakeAmount] = useState(0);
    const tokenAccount: any = new PublicKey('HZ1JovNiVvGrGNiiYvEozEVgZ58xaU3RKwX8eACQBCt3');

    useEffect(() => {
        async function getTokenBalanceWeb3() {
            // const info: any = await connection.getTokenAccountsByOwner(
            const info: any = await connection.getParsedTokenAccountsByOwner(wallet.publicKey, {
                mint: tokenAccount,
            });

            setTokenBalance(info.value[0].account.data.parsed.info.tokenAmount.uiAmount);
            setTokenBalanceOriginal(info.value[0].account.data.parsed.info.tokenAmount.amount);
            setStakeAmount(info.value[0].account.data.parsed.info.tokenAmount.uiAmount);
        }
        getTokenBalanceWeb3();
    }, [wallet]);

    const setHalfStake = () => {
        if (!tokenBalance) return;

        const half: any = parseFloat(tokenBalance) / 2;
        setStakeAmount(half.toFixed(6));
    };

    const setMaxStake = () => {
        if (!tokenBalance) return;
        setStakeAmount(tokenBalance);
    };

    const stake = async () => {
        if (!publicKey || !signTransaction) throw new WalletNotConnectedError();

        const toPublicKey = new PublicKey('EPLzxNmqDzq3dssniUDmoDXrSNsDov7RzayXssA8dQ7m');
        const mint = new PublicKey('HZ1JovNiVvGrGNiiYvEozEVgZ58xaU3RKwX8eACQBCt3');

        const fromTokenAccount = await getOrCreateAssociatedTokenAccount(
            connection,
            publicKey as any,
            mint,
            publicKey,
            signTransaction as any
        );
        const toTokenAccount = await getOrCreateAssociatedTokenAccount(
            connection,
            publicKey as any,
            mint,
            toPublicKey,
            signTransaction as any
        );

        console.log(typeof tokenBalance);

        const transaction = new Transaction().add(
            createTransferInstruction(
                fromTokenAccount.address, // source
                toTokenAccount.address, // dest
                publicKey,
                tokenBalanceOriginal,
                [],
                TOKEN_PROGRAM_ID
            )
        );

        const blockHash = await connection.getRecentBlockhash();
        transaction.feePayer = await publicKey;
        transaction.recentBlockhash = await blockHash.blockhash;
        const signed = await signTransaction(transaction);

        await connection.sendRawTransaction(signed.serialize());
    };

    return (
        // <div>
        //     {wallet.connected ? (
        //         <div>
        //             <p>Wallet Address: {wallet.publicKey.toBase58()}</p>
        //             <p>Balance: {balance} SOL</p>
        //         </div>
        //     ) : (
        //         <button onClick={() => wallet.connect()}>Connect Wallet</button>
        //     )}
        // </div>
        <div className="mx-auto max-w-xl text-center leading-6">
            {/* <pre>{JSON.stringify({ tokenBalance, tokenBalanceOriginal }, null, 2)}</pre> */}
            <div className="mb-4 flex items-end justify-between md:items-center ">
                <label htmlFor="amount" className="block ">
                    Amount (PYTH)
                </label>
                <div className="ml-auto mr-0 flex flex-col-reverse items-end space-x-2 md:flex-row md:items-center">
                    <p className="mt-2 md:mt-0">Balance: {tokenBalance}</p>
                    <div className="mb-2  flex space-x-2 md:mb-0">
                        <button className="outlined-btn hover:bg-darkGray4" onClick={setHalfStake}>
                            Half
                        </button>
                        <button className="outlined-btn hover:bg-darkGray4" onClick={setMaxStake}>
                            Max
                        </button>
                    </div>
                </div>
            </div>
            <input
                type="text"
                name="amount"
                id="amount"
                autoComplete="amount"
                className="input-no-spin mb-8 mt-1 block h-14 w-full rounded-full bg-darkGray4 px-4 text-center text-lg font-semibold  focus:outline-none"
                value={stakeAmount}
                onChange={(e) => setStakeAmount(parseInt(e.target.value))}
            />
            <div className="flex items-center justify-center ">
                <button className="action-btn text-base " onClick={stake}>
                    Stake
                </button>
            </div>
        </div>
    );
};

const SendTokenButton = () => {
    const { connection } = useConnection();
    const { publicKey, signTransaction, sendTransaction } = useWallet();

    const sendToken = async () => {
        if (!publicKey || !signTransaction) throw new WalletNotConnectedError();

        const toPublicKey = new PublicKey('9Y7JxxexoCDQLKaduVapJrBkDLvJh59JHveGwMyCzQBY');
        const mint = new PublicKey('HZ1JovNiVvGrGNiiYvEozEVgZ58xaU3RKwX8eACQBCt3');

        const fromTokenAccount = await getOrCreateAssociatedTokenAccount(
            connection,
            publicKey as any,
            mint,
            publicKey,
            signTransaction as any
        );
        const toTokenAccount = await getOrCreateAssociatedTokenAccount(
            connection,
            publicKey as any,
            mint,
            toPublicKey,
            signTransaction as any
        );

        const transaction = new Transaction().add(
            createTransferInstruction(
                fromTokenAccount.address, // source
                toTokenAccount.address, // dest
                publicKey,
                1 * LAMPORTS_PER_SOL,
                [],
                TOKEN_PROGRAM_ID
            )
        );

        const blockHash = await connection.getRecentBlockhash();
        transaction.feePayer = await publicKey;
        transaction.recentBlockhash = await blockHash.blockhash;
        const signed = await signTransaction(transaction);

        await connection.sendRawTransaction(signed.serialize());
    };

    return <button onClick={sendToken}>Send Token</button>;
};

